import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Button, 
  Stepper, 
  Step, 
  StepLabel,
  Paper,
  TextField,
  IconButton,
  Chip
} from '@mui/material';
import { 
  Description as TemplateIcon,
  ArrowBack as ArrowBackIcon
} from '@mui/icons-material';
import PdfPageEditor from '../../../../components/PdfPageEditor';
import { Template } from '../TemplateList/types';
import { ExtractedField } from '../UploadTemplate/types';
import { toast } from 'react-toastify';
import PaymentScheduleStep, { PaymentItem } from '../PaymentScheduleStep';
import { useTranslation } from 'react-i18next';

// M rng interface Template  thm trng extractedFields
interface ExtendedTemplate extends Template {
  extractedFields?: ExtractedField[];
}

interface CreateContractProps {
  templates: ExtendedTemplate[];
  onBack: () => void;
  onCreate: (contract: any) => void;
}

const CreateContract: React.FC<CreateContractProps> = ({ 
  templates, 
  onBack,
  onCreate 
}) => {
  const { t } = useTranslation();
  const [activeStep, setActiveStep] = useState(0);
  const [selectedTemplate, setSelectedTemplate] = useState<ExtendedTemplate | null>(null);
  // S dng formData  lu cc gi tr nhp vo form
  const [formData, setFormData] = useState<Record<string, string>>({});
  // Lu ni dung hp ng  hin th trong PdfPageEditor
  const [contractContent, setContractContent] = useState<string[]>(['']);
  // Lu cc t thanh ton
  const [payments, setPayments] = useState<PaymentItem[]>([]);
  
  // Hm to ni dung HTML cho hp ng t formData
  const generateContractHTML = React.useCallback(() => {
    // To s hp ng
    const contractNumber = `HD${new Date().getFullYear()}${(new Date().getMonth() + 1).toString().padStart(2, '0')}${new Date().getDate().toString().padStart(2, '0')}001`;
    
    // nh dng s tin
    const formatCurrency = (amount: number) => {
      return new Intl.NumberFormat('vi-VN').format(amount);
    };
    
    // To HTML cho bng thanh ton
    const paymentScheduleHTML = payments.length > 0 ? `
      <h3 style="font-weight: bold; margin-top: 30px; margin-bottom: 20px;">IU 2: LCH THANH TON</h3>
      <table style="width: 100%; border-collapse: collapse; margin-bottom: 20px;">
        <thead>
          <tr style="background-color: #f2f2f2;">
            <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">STT</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">iu kin</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">S tin</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">Ngy thanh ton</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">Ghi ch</th>
          </tr>
        </thead>
        <tbody>
          ${payments.map((payment, index) => `
            <tr>
              <td style="border: 1px solid #ddd; padding: 8px;">${index + 1}</td>
              <td style="border: 1px solid #ddd; padding: 8px;">${payment.condition}</td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">${formatCurrency(payment.amount)} VN</td>
              <td style="border: 1px solid #ddd; padding: 8px;">${payment.paymentDate ? new Date(payment.paymentDate).toLocaleDateString('vi-VN') : ''}</td>
              <td style="border: 1px solid #ddd; padding: 8px;">${payment.note}</td>
            </tr>
          `).join('')}
          <tr style="font-weight: bold; background-color: #f9f9f9;">
            <td style="border: 1px solid #ddd; padding: 8px;" colspan="2">Tng cng</td>
            <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">${formatCurrency(payments.reduce((sum, payment) => sum + payment.amount, 0))} VN</td>
            <td style="border: 1px solid #ddd; padding: 8px;" colspan="2"></td>
          </tr>
        </tbody>
      </table>
    ` : '';
    
    // To HTML cho hp ng
    return `
      <div style="text-align: center; margin-bottom: 20px;">
        <h2 style="font-weight: bold; text-transform: uppercase;">Cng Ha X Hi Ch Ngha Vit Nam</h2>
        <h3 style="font-weight: bold;">c Lp  T Do  Hnh Phc</h3>
        <p style="margin-bottom: 20px;">__________</p>
        <h1 style="font-weight: bold; text-transform: uppercase; margin-bottom: 20px;">HP NG NGUYN TC CUNG CP DCH V PHN MM</h1>
        <h2 style="font-weight: bold; margin-bottom: 30px;">S: ${contractNumber}</h2>
      </div>

      <p>
        Cn c vo Lut Thng Mi ca Quc hi nc Cng Ha X Hi Ch Ngha Vit Nam c ban hnh ngy 14/06/2005, c hiu lc t ngy 01/01/2006.
      </p>
      
      ${Object.entries(formData).map(([field, value]) => `<p>Cn c vo ${field}: <strong>${value}</strong></p>`).join('\n')}

      <h3 style="font-weight: bold; margin-top: 30px; margin-bottom: 20px;">IU 1: NI DUNG HP NG</h3>
      <p>
        Bn A y thc cho bn B cng vic xy dng v pht trin phn mm v cung cp cc dch v lin quan n phn mm ca bn A.
      </p>
      <p>
        Bn B s chu trch nhim xy dng phn mm ng nh tha thun ca c Bn A v Bn B.
      </p>
      <p>
        V chi tit ni dung cng vic v chi ph thc hin s c trnh by trong tng Ph lc c th.
      </p>
      
      ${paymentScheduleHTML}
    `;
  }, [formData, payments]);
  
  // Cp nht ni dung hp ng khi formData thay i
  useEffect(() => {
    // Ch cp nht khi  in t nht mt trng v  n bc xem trc
    if (activeStep === 3 && Object.keys(formData).length > 0 && Object.values(formData).some(value => value)) {
      setContractContent([generateContractHTML()]);
    }
  }, [formData, activeStep, generateContractHTML]);
  
  // Bc 1: Chn template
  const renderTemplateSelection = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
        {t('pages.contracts.createContract.templateSelection.title')}
      </Typography>
      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(280px, 1fr))', gap: 16 }}>
        {templates.map((template) => (
          <div 
            key={template.id} 
            style={{ 
              marginBottom: '16px'
            }}>
          
            <Paper 
              elevation={selectedTemplate?.id === template.id ? 4 : 1}
              sx={{ 
                p: 2, 
                cursor: 'pointer',
                border: selectedTemplate?.id === template.id ? '2px solid #1976d2' : '1px solid #e0e0e0',
                height: '100%',
                display: 'flex',
                flexDirection: 'column'
              }}
              onClick={() => setSelectedTemplate(template)}
            >
              <Box display="flex" alignItems="center" mb={1}>
                <TemplateIcon color="primary" sx={{ mr: 1 }} />
                <Typography variant="subtitle1" fontWeight="bold">
                  {template.name}
                </Typography>
              </Box>
              <Typography variant="body2" color="textSecondary" sx={{ flexGrow: 1 }}>
                {template.name} - {template.type}
              </Typography>
              <Chip 
                label={t('pages.contracts.createContract.templateSelection.chipLabel', { count: template.placeholderCount })}
                size="small" 
                sx={{ mt: 1, alignSelf: 'flex-start' }} 
              />
            </Paper>
          </div>
        ))}
      </div>
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
        <Button 
          variant="contained" 
          disabled={!selectedTemplate}
          onClick={() => {
            // Khi to formData ch vi cc trng c trch xut t template
            const formFields: Record<string, string> = {};
            
            // Thm cc trng t template nu c
            if (selectedTemplate?.extractedFields && selectedTemplate.extractedFields.length > 0) {
              selectedTemplate.extractedFields.forEach((field: ExtractedField) => {
                formFields[field.placeholder] = '';
              });
            } else {
              // Nu khng c trng no c trch xut, hin th thng bo
              toast.error(t('pages.contracts.createContract.alerts.noFieldsFound'));
              return;
            }
            
            // Khi to formData vi cc trng cn in t template
            
            // Khi to formData vi cc trng cn in
            setFormData(formFields);
            // Chuyn sang bc in thng tin
            setActiveStep(1);
          }}
        >
          {t('pages.contracts.createContract.buttons.continue')}
        </Button>
      </Box>
    </Box>
  );

  // Bc 2: in thng tin qua form trc tip
  const renderContractForm = () => {
    // Kim tra xem  in  thng tin cha
    const checkFormCompletion = () => {
      const fieldNames = Object.keys(formData);
      const missingFields = fieldNames.filter(field => !formData[field]);
      return missingFields.length === 0;
    };

    // X l khi ngi dng thay i gi tr ca mt trng
    const handleFieldChange = (fieldName: string, value: string) => {
      setFormData(prev => ({
        ...prev,
        [fieldName]: value
      }));
    };

    // X l khi ngi dng nhn nt Tip tc
    const handleContinue = () => {
      if (checkFormCompletion()) {
        setActiveStep(2); // Chuyn sang bc hng mc thanh ton
      } else {
        // Show localized toast if required fields are missing
        toast.error(t('pages.contracts.createContract.form.requiredHelper'));
      }
    };

    return (
      <Box sx={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <IconButton onClick={() => setActiveStep(0)} sx={{ mr: 1 }}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6">
            {t('pages.contracts.createContract.form.title')}: {selectedTemplate?.name}
          </Typography>
        </Box>
        
        <Paper 
          variant="outlined" 
          sx={{ 
            p: 3, 
            flexGrow: 1, 
            mb: 2, 
            overflowY: 'auto',
            maxHeight: '600px'
          }}
        >
          <Typography variant="h6" gutterBottom>
            {t('pages.contracts.createContract.form.sectionTitle')}
          </Typography>
          
          <Typography variant="body2" color="text.secondary" paragraph>
            {t('pages.contracts.createContract.form.description')}
          </Typography>
          
          <Box sx={{ 
            display: 'grid', 
            gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' },
            gap: 3,
            mt: 2
          }}>
            {Object.keys(formData).map((fieldName) => (
              <TextField
                key={fieldName}
                label={fieldName}
                variant="outlined"
                fullWidth
                value={formData[fieldName]}
                onChange={(e) => handleFieldChange(fieldName, e.target.value)}
                required
                error={!formData[fieldName]}
                helperText={!formData[fieldName] ? t('pages.contracts.createContract.form.requiredHelper') : ''}
                sx={{ mb: 2 }}
              />
            ))}
          </Box>
        </Paper>
        
        <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Button 
            variant="outlined" 
            onClick={() => setActiveStep(0)}
            startIcon={<ArrowBackIcon />}
          >
            {t('pages.contracts.createContract.buttons.back')}
          </Button>
          <Button 
            variant="contained" 
            color="primary"
            onClick={handleContinue}
            disabled={!checkFormCompletion()}
          >
            {t('pages.contracts.createContract.buttons.continue')}
          </Button>
        </Box>
      </Box>
    );
  };

  // X l to hp ng
  const handleCreateContract = () => {
    if (!selectedTemplate) return;
    
    const newContract = {
      id: `C${Math.floor(1000 + Math.random() * 9000)}`,
      name: `${t('pages.contracts.createContract.generatedNamePrefix')} ${selectedTemplate?.name} - ${new Date().toLocaleDateString()}`,
      templateId: selectedTemplate?.id || '',
      templateName: selectedTemplate?.name || '',
      status: 'draft',
      partyA: t('pages.contracts.createContract.sample.partyA'),
      partyB: t('pages.contracts.createContract.sample.partyB'),
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      createdBy: t('pages.contracts.createContract.sample.currentUser'),
      // Lu formData v payments vo contract
      contractData: formData,
      payments: payments
    };
    
    // Gi hm to hp ng
    onCreate(newContract);
    toast.success(t('pages.contracts.notifications.createContractSuccess'));
  };

  // Bc 3: Xem trc v xc nhn
  const renderPreview = () => {
    if (!selectedTemplate) return null;

    return (
      <Box>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <IconButton onClick={() => setActiveStep(2)} sx={{ mr: 1 }}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6">
            {t('pages.contracts.createContract.preview.title')}
          </Typography>
        </Box>
        
        <Box sx={{ border: '1px solid #e0e0e0', borderRadius: 1, padding: 0, overflow: 'hidden', mb: 3 }}>
          <PdfPageEditor 
            initialContent={contractContent}
            onSave={setContractContent}
            onExportPdf={() => {
              toast.success(t('pages.contracts.createContract.notifications.exportPdfSuccess'));
            }}
          />
        </Box>
        
        <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Button 
            variant="outlined" 
            onClick={() => setActiveStep(1)}
            startIcon={<ArrowBackIcon />}
          >
            {t('pages.contracts.createContract.buttons.back')}
          </Button>
          <Button
            variant="contained"
            color="primary"
            onClick={handleCreateContract}
          >
            {t('pages.contracts.createContract.buttons.create')}
          </Button>
        </Box>
      </Box>
    );
  };
  // Bc 3: Thm hng mc thanh ton
  const renderPaymentSchedule = () => {
    return (
      <PaymentScheduleStep
        payments={payments}
        onPaymentsChange={setPayments}
        onBack={() => setActiveStep(1)}
        onNext={() => setActiveStep(3)}
      />
    );
  };

  const steps = [
    { label: t('pages.contracts.createContract.steps.selectTemplate'), component: renderTemplateSelection },
    { label: t('pages.contracts.createContract.steps.fillInfo'), component: renderContractForm },
    { label: t('pages.contracts.createContract.steps.paymentItems'), component: renderPaymentSchedule },
    { label: t('pages.contracts.createContract.steps.previewConfirm'), component: renderPreview },
  ];

  return (
    <Box sx={{ width: '100%' }}>
      <Stepper activeStep={activeStep} sx={{ mb: 4 }}>
        {steps.map((step, index) => (
          <Step key={step.label} completed={activeStep > index}>
            <StepLabel>{step.label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      
      <Box sx={{ mt: 2 }}>
        {steps[activeStep].component()}
      </Box>
    </Box>
  );
};

// Component ny  c thay th bng PdfPageEditor trong bc xem trc
// const ContractPreview: React.FC = () => {
//   return null;
// };

export default CreateContract;
